import React, {useEffect, useState} from 'react'
import Recipe from './Recipe'
import './App.css';

const App = () => {

  const APP_ID = '0c3766f3'
  const APP_KEY = 'da87522488899918f3fd4b96f5b5c3f5' 

  const [recipes, setRecipes] = useState([])
  const [search, setSearch] = useState("")
  const [query, setQuery] = useState("chicken")  // Only when the search button is clicked

  useEffect(() => {
    getRecipes();
  }, [query]) // Only when counter changes useEffect should run

  const getRecipes = async() => {
    const response = await fetch(`https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`)
    const data = await response.json()
    setRecipes(data.hits)
  }


  const searchHandler = e => {
    setSearch(e.target.value)
  }

  const getRequestHandler = e => {
    e.preventDefault()
    setQuery(search)
    setSearch("")
  }

  return (
    <div className="App">
      <form onSubmit={getRequestHandler} className="search-form">
        <input className="search-bar" type="text" value={search} onChange={searchHandler} />
        <button 
          className="search-button" 
          type="submit">
            Search
        </button>
      </form>
      <div className="recipes">
        {recipes.map((recipe) => (
          <Recipe 
            key={recipe.recipe.label}
            title={recipe.recipe.label} 
            calories={recipe.recipe.calories} 
            image={recipe.recipe.image}
            ingredients={recipe.recipe.ingredients}
          />
        ))}
      </div>
      

    </div>
  );
}

export default App;
